import React from 'react';

import { Table, TableBody, TableCell, TableFooter, TableHead, TableRow, Typography } from '@material-ui/core';

import { foodList } from '../types/FoodList';

export interface Props {
  units: { [key: number]: number | undefined };
}

export const ResultsTable: React.FunctionComponent<Props> = props => (
  <>
    <Typography variant="display1">Ergebnis</Typography>
    <Table padding="dense">
      <TableHead>
        <TableRow>
          <TableCell>Nahrungsmittel</TableCell>
          <TableCell align="right">Menge</TableCell>
          <TableCell align="right">Kalorien</TableCell>
          <TableCell align="right">Salz</TableCell>
          <TableCell align="right">Zucker</TableCell>
        </TableRow>
      </TableHead>

      <TableBody>
        {foodList.map((food, idx) => {
          const units = props.units[idx];

          if (units) {
            const weight = units * food.info.gPerUnit;
            const factor = weight / 100;
            const pretty = (value: number) => (factor * value).toFixed(2);
            return (
              <TableRow>
                <TableCell>{food.info.nicename}</TableCell>
                <TableCell align="right">{weight} g</TableCell>
                <TableCell align="right">{pretty(food.metrics.kilokalorien)} kcal</TableCell>
                <TableCell align="right">{pretty(food.metrics.salz)} g</TableCell>
                <TableCell align="right">{pretty(food.metrics.zucker)} g</TableCell>
              </TableRow>
            );
          } else {
            return null;
          }
        })}
      </TableBody>

      <TableFooter>
        <TableRow>
          <TableCell>Summe</TableCell>
          <TableCell align="right">?</TableCell>
          <TableCell align="right">?</TableCell>
          <TableCell align="right">?</TableCell>
          <TableCell align="right">?</TableCell>
        </TableRow>
      </TableFooter>
    </Table>
  </>
);
